*{margin:0;padding:0;font-family:Consolas, "Andale Mono WT", "Andale Mono", "Lucida Console", "Lucida Sans Typewriter", "DejaVu Sans Mono", "Bitstream Vera Sans Mono", "Liberation Mono", "Nimbus Mono L", Monaco, "Courier New", Courier, monospace;margin:0;padding:0;box-sizing:border-box}body{height:700px;background:linear-gradient(18deg, #e37682, #a58fe9);background-repeat:no-repeat;justify-content:space-evenly;align-items:center}.clear{clear:both}.slide-toggle{display:none}.slidemenu{font-family:arial, sans-serif;max-width:600px;margin:50px auto;overflow:hidden}.slidemenu label{width:25%;text-align:center;display:block;float:left;color:#333;opacity:0.2}.slidemenu label:hover{cursor:pointer;color:#666}.slidemenu label span{display:block;padding:10px}.slidemenu label .icon{font-size:20px;border:solid 2px #333;text-align:center;height:50px;width:50px;display:block;margin:0 auto;line-height:50px;border-radius:50%}.slider{width:100%;height:5px;display:block;background:#ccc;margin-top:10px;border-radius:5px}.slider .bar{width:25%;height:5px;background:#333;border-radius:5px}.slidemenu label,.slider .bar{transition:all 500ms ease-in-out;-webkit-transition:all 500ms ease-in-out;-moz-transition:all 500ms ease-in-out}.slidemenu .slide-toggle:checked+label{opacity:1}.slidemenu #slide-item-1:checked ~ .slider .bar{margin-left:0}.slidemenu #slide-item-2:checked ~ .slider .bar{margin-left:25%}.slidemenu #slide-item-3:checked ~ .slider .bar{margin-left:50%}.slidemenu #slide-item-4:checked ~ .slider .bar{margin-left:75%}*{margin:0;padding:0;box-sizing:border-box}h1{font-size:2.5rem;font-family:'Montserrat';font-weight:normal;color:#444;text-align:center;margin:2rem 0}.wrappering{width:90%;margin:0 auto;max-width:150rem}.cols{display:flex;flex-wrap:wrap;justify-content:center}.col{width:calc(25% - 2rem);margin:1rem;cursor:pointer}.container{transform-style:preserve-3d;perspective:1000px}.SeaGreen{color:#2e8b57}.blue{color:#29b6f6}.RebeccaPurple{color:#639}.green{color:#9ccc65}.purpl